.container {
    display: flex;
    justify-content: center;
    align-items: flex-end;

    grid-column: 3;    

    position: relative;
    bottom: 19px;

    width: 50px;
    height: 50px;

    overflow: visible;
}

.container input {
    -webkit-appearance: none;
    display: none;

    position: absolute;
    bottom: 130px;
    right: -74px;

    width: 200px;
    height: 20px;
    border-radius: 4px;

    transform: rotate(270deg);

    background-color: rgb(0, 0, 0);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
    
    z-index: 1000000;
    cursor: pointer;
    overflow: visible;    
}

.container input::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;

    width: 10px;
    height: 20px;
    
    border-radius: 4px;

    background: white;
}

.container input::-moz-range-thumb {
    width: 25px;
    height: 15px;
    border-radius: 20px;

    background-color: white;
}

.container:hover input {
    display: block;
}

.container button > img {
    width: 30px;
    filter:invert();
}

@media  (orientation: portrait) {
    .container {
        grid-row: 2;
        grid-column: 1;
    }
}