.bar {
    display: block;
    -webkit-appearance: none;
    appearance: none;    

    width: 100%;

    background-color: rgb(0, 0, 0);    

    border-radius: 4px;

    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);

    cursor: pointer;    
    overflow: visible;
}

.bar::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;

    width: 15px;
    height: 15px;

    border-radius: 4px;

    background: white;
}

.bar::-moz-range-thumb {
    width: 25px;
    height: 15px;
    
    border-radius: 20px;

    background-color: white;
}