.container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;

    height: 100vh;
    padding-top: 100px;
    padding-bottom: 150px;

    overflow-y: scroll;
}

.container p {
    font-family: 'Major Mono Display', monospace;
    font-size: 1.75rem;
    font-weight: bold;
    text-shadow: 0px 0px 15px black;

    overflow: visible;
}

.cover img{
    width: 80%;
    max-width: 500px;

    box-shadow: 0 0 15px black;
}

.cover {
    margin: 10px;    
    text-align: center;
}

@media screen and (max-height: 560px) {
    .cover img {
        max-width: 200px;
    }

    .container p {
        height: 40px;        
        font-size: 1rem;
    }

    .cover {
        width: 200px;
    }
}