.container {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;
    
    overflow: visible;
}
.container div {
    overflow: visible;
}

.container h1 {
    width: 100%;

    font-size: min(14rem, 20vw);
    text-shadow: 0px 0px 15px black;
    text-align: center;
    
    overflow: visible;
}

.container > div > div {
    display: flex;    
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;    

    width: 100%;
    height: 100px;

    color: white;

    font-size: clamp(1rem, 2vw, 2rem);
}

@media screen and (max-height: 600px) {
    .container h1 {
        font-size: min(14rem, 30vh);
    }
}